.employee {
    &-list {
        border-radius: var(--border-radius);

        &-item {
            // Overrides
            --img-thumb-size-md: 45px;
            --icon-size: 45px;

            display: flex;
            align-items: center;

            &:hover {
                background-color: var(--blue-50);
            }

            .content {
                flex-basis: 100%;
                padding: var(--sp-0);
                margin-left: var(--sp-1);
                border-bottom: 1px solid var(--border-color);
            }

            .miq-img-square {
                border-radius: 50%;
            }
        }
    }
}
